  
  body {
    background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
    background-color: #060105;
    background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }
  
  a {
    color: white !important;
    text-decoration: none !important;
  }
  .red {
      background-color: red;
      display: inline;
  }
  
  header {
    color: white;
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
    /* background-image: linear-gradient(to top, #30cfd0 0%, #330867 100%); */
    position: relative;
    /* background-color: #0E0C38; */
  }
  
  nav {
    width: 100%;
    height: 15vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
  }
  
  .active {
    background-color: #FF0513;
    /* color: #0E0C38; */
    color: white;
  }
  
  .logo a {
    font-size: 1.5rem;
    text-transform: uppercase;
  }
  
  ul li {
    list-style: none;
    display: inline-block;
    padding: 0 15px;
  }
  
  ul li:hover {
    border-bottom: 1px solid #FF0513;
  }
  
  
  .contact-btn a{
    border: 2px solid white;
    padding: 10px;
    border-radius: 20px;
  }
  
  .contact-btn a:hover {
    background-color:  #FF0513;
    color: #0E0C38;
  }
  
  .main-image {
    position: absolute;
    top: 20%;
    right: 5%;
  }
  
  .main-image img {
    border-radius: 60% 40% 70% 30% / 29% 60% 40% 71% ;
    /* border: 2px  solid rgb(255, 255, 255); */
    transition: border-radius 1s ease;
    box-shadow:
    20px 5px 40px #CF77F3,
      0px 5px 40px #009BFF,
     -20px 5px 40px #2AC9DB;
  }
  
  .main-image img:hover {
    border-radius: 50%;
    /* border: 2px  solid rgb(255, 255, 255); */
    border-radius: 50%;
  }
  
  .name-tag {
    
    position: absolute;
    top: 20%;
    left: 5%;
  }
  
  .name {
    font-size: 5rem;
    font-family: 'Lora', serif !important;
  }
  
  .heading-line {
    font-size: large;
  }
  
  .svg-dott, .svg-dott2 {
    top: 20%;
    position: absolute;
    right: 0px;
  }
  
  #circle {
    text-align: right;
    margin-top: 15%;
    transition: width 1s ease;
  }
  
  #circle:hover {
    width: 20%;
  }
  
  .socials a{
    font-size: 3rem;
    padding: 32px;
  }
  
  .fa-twitter:hover {
    color: #00acee;
    border-bottom: 1px solid #FF0513;
  }
  
  .fa-github:hover {
    color: black;
    border-bottom: 1px solid #FF0513;
  }
  .fa-linkedin:hover {
    color:   rgb(46, 83, 204);
    border-bottom: 1px solid #FF0513;
  }
  
  #resume-btn {
    margin-top: 20px;
    margin-left: 50%;
    transform: translate(-50%, -50%);
  }
  
  #resume-btn a{
    border: 2px solid white;
    border-radius: 15px;
    padding: 5px;
    transition: border-radius 1s ease;
  }
  
  #resume-btn a:hover {
    background-color: red;
    border-radius: 0px;
  }
  
  #line {
    position: absolute;
  }
  
  /* For mobile devices */
  @media only screen and (max-width: 600px) {
  
    .contact-btn a, .socials a, ul li{
        padding: 2px;
    }
    .name {
        font-size: 3rem;
    }
    .hello, .heading-line {
        font-size: 0.9rem;
    }
    .socials a {
        font-size: 3rem;
    }
    ul li {
        font-size: 1rem;
        padding: 5px;
    }
    .logo a {
        font-size: 1.5rem;
        padding: 2px;    
    }
    .main-image {
        left: 50%;
        top: 30%;
        position: absolute;
        margin: auto;
        transform: translate(-50%, -50%);
    }
    .main-image img {
        width: 150px;
    }
    #circle {
        margin-top: 70%;
        width: 60px;
        height: 60px;
    }
    .svg-dott {
        position: absolute;
        right: 20%;
    }
    .svg-dott2 {
        display: none;
    }
    .name-tag{
        top: 60% !important;
        left: 50%;
        transform: translate(-50%, -50%);
        display: inline-block;
    }

    
  }

  @media only screen and (max-width: 1000px) {
    .name {
      font-size: 3rem;
    }
    .hello, .heading-line {
      font-size: 0.9rem;
    }
    .main-image {
      left: 50%;
      top: 30%;
      position: absolute;
      margin: auto;
      transform: translate(-50%, -50%);
  }
  .name-tag{
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: inline-block;
  }
  }
  
  