#contact-section {
    height: 100vh;
    color: rgb(255, 255, 255);
    overflow-x: hidden;
}

.contact-logo {
    text-align: center;
    text-transform: uppercase;
    font-family: 'Hachi Maru Pop', cursive;
}

.contact-box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;

}

.contact-inputs input {
    width: 40vw;
    height: 7vh;
}

.contact-inputs textarea{
    width: 40vw;
    height:30vh;
} 

.contact-inputs input, .contact-inputs textarea {
    border: 2px solid rgb(0, 0, 0);
    border-radius: 10px;
    margin-bottom: 30px;
    margin-top: 30px;
}

.center-mail-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    
}

