@media only screen and (max-width: 600px) {
    .contact-inputs input {
        width: 80vw;
        height: 7vh;
    }
    
    .contact-inputs textarea{
        width: 80vw;
        height:30vh;
    } 
    
    .contact-inputs input, .contact-inputs textarea {
        border: 2px solid rgb(0, 0, 0);
        border-radius: 10px;
        margin-bottom: 30px;
        margin-top: 30px;
    }
}