.project-of {
    text-align: center;
}

.project-shell-1 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: no-wrap;
}

.project-item {
    height: 50vh !important;
    width: 100% !important;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    border-radius: 20px;
}

.project-description *{
    color: white;
    background-color: rgb(0, 0, 0, .4);

}
.github-icon {
    font-size: 2rem;
}

.web-text-contain {
    display: flex;
    justify-content: center;
    margin: 10px;
}
#web {
    text-align: center;
    padding: 5px;
    color: rgb(255, 255, 255) !important;
    background-color: rgb(0, 0, 0);
    border-radius: 5px;
}


.rec-dot {
    background-color: white;
}